div.image {
  height: 85px;
  width: 85px;
  text-align: center;
  line-height: 200px;
  background-color: azure;
  border: 1px solid black;
  margin: 10px;
}

#board {
  display: grid;
  grid-template-columns: 115px 115px 115px 115px 115px 115px 115px 115px 115px 115px;
}

header {
  display: flex;
  justify-content: space-between;
  font-size: 20px;
  text-align: center;
  padding: 0 100px 0 100px;
}

.grid-item {
  padding: 20px;
  font-size: 30px;
  text-align: center;
}

.grid-container{
  display: inline-block;
}

.App{
  text-align: center;
}